.task {
  box-sizing: border-box;
  width: 180px;
  height: 70px;
  background-color: #3c526b;
  border-radius: 5px;
  border: 1px solid #2d4054;
  text-align: center;
  color: #fff;
}

.task-active {
  border: 3px solid #4773e6;
}

.task-inside {
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
}

.task-text {
  transform: translate(-50%, -50%);
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
}

.remove-wrapper {
  position: absolute;
  right: 10px;
  top: -30px;
  font-weight: 700;
}

.right-arrow-wrapper {
  position: absolute;
  right: -55px;
  top: -5px;
}
.arrow-right {
  width: 0;
  height: 0;
  margin-top: 10px;
  border-style: solid;
  border-width: 12px 0 12px 24px;
  border-color: transparent transparent transparent #ff0080;
}
